import React, { useState, useEffect } from 'react';
import {
  Box,
  AppBar,
  Toolbar,
  Typography,
  Container,
  IconButton,
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Divider,
  Badge
  
} from '@mui/material';
import {
  Menu as MenuIcon,
  Dashboard,
  LocalOffer,
  People,
  Settings,
  Help,
  Inventory,
  ShoppingCart,
  Notifications
} from '@mui/icons-material';
import NavMenu from './NavMenu';
import { useAuth0 } from '@auth0/auth0-react';
import { useRouter } from 'next/router';
import CartDrawer from '../cart/CartDrawer';
import { useCart } from '../../context/CartContext';
import { People as PeopleIcon } from '@mui/icons-material';
import DigitalRain from '../effects/DigitalRain';


interface DashboardLayoutProps {
  children: React.ReactNode;
}

const menuItems = [
  { text: 'Dashboard', icon: <Dashboard />, path: '/dashboard' },
  { text: 'Products', icon: <Inventory />, path: '/products' },
  { text: 'Orders', icon: <ShoppingCart />, path: '/orders' },
  { text: 'Users', icon: <PeopleIcon />, path: '/users' },
  { text: 'Discounts', icon: <LocalOffer />, path: '/analytics' },
  { text: 'Settings', icon: <Settings />, path: '/settings' },
  { text: 'Help', icon: <Help />, path: '/help' }
] as const;  // 添加 as const 使类型更严格

interface DashboardLayoutProps {
  children: React.ReactNode;
}

const DashboardLayout: React.FC<DashboardLayoutProps> = ({ children }) => {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [cartOpen, setCartOpen] = useState(false);
  const router = useRouter();
  // 移除 Auth0 相关代码
  const isAuthenticated = true; // 临时设置
  const isLoading = false; // 临时设置
  const { cartItems } = useCart();

  const toggleDrawer = () => {
    setDrawerOpen(!drawerOpen);
  };

  // 移除认证相关的检查
  return (
    <Box sx={{ display: 'flex', minHeight: '100vh' }}>
      {/* Sidebar */}
      <Drawer
        variant="permanent"
        sx={{
          width: 240,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: 240,
            boxSizing: 'border-box',
            background: '#ffffff',
            position: 'relative',
            overflow: 'hidden'
          },
        }}
      >
        <DigitalRain />
        <Box sx={{ 
          p: 2, 
          display: 'flex', 
          alignItems: 'center', 
          background: 'linear-gradient(90deg, #3b82f6 0%, #8b5cf6 100%)',
          color: 'white',
          position: 'relative',
          zIndex: 1
        }}>
          <Typography 
            variant="h5" 
            sx={{ 
              fontWeight: 600,
              letterSpacing: 1,
              background: 'linear-gradient(90deg, #ffffff 0%, #e2e8f0 100%)',
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent'
            }}
          >
            AlwaysRight
          </Typography>
        </Box>
        <Divider />
        <List sx={{ position: 'relative', zIndex: 1 }}>
          {menuItems.map((item) => (
            <ListItem key={item.text} disablePadding>
              <ListItemButton
                onClick={() => {
                  router.push(item.path);
                  toggleDrawer();
                }}
                selected={router.pathname === item.path}
                sx={{
                  '&.Mui-selected': {
                    backgroundColor: 'rgba(59, 130, 246, 0.08)',
                    '&:hover': {
                      backgroundColor: 'rgba(59, 130, 246, 0.12)',
                    }
                  },
                  '&:hover': {
                    backgroundColor: 'rgba(59, 130, 246, 0.04)',
                  }
                }}
              >
                <ListItemIcon 
                  sx={{ 
                    color: router.pathname === item.path ? '#3b82f6' : 'inherit'
                  }}
                >
                  {item.icon}
                </ListItemIcon>
                <ListItemText 
                  primary={item.text} 
                  sx={{
                    '& .MuiTypography-root': {
                      color: router.pathname === item.path ? '#3b82f6' : 'inherit',
                      fontWeight: router.pathname === item.path ? 600 : 400
                    }
                  }}
                />
              </ListItemButton>
            </ListItem>
          ))}
        </List>
      </Drawer>

      {/* Main content */}
      <Box sx={{ flexGrow: 1, display: 'flex', flexDirection: 'column' }}>
        <AppBar 
          position="fixed" 
          color="default"
          elevation={1}
          sx={{ 
            ml: '240px', 
            width: `calc(100% - 240px)`,
          }}
        >
          <Toolbar sx={{ justifyContent: 'flex-end' }}>
            {/* 右侧图标 */}
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              {/* 购物车图标 */}
              <IconButton color="inherit" onClick={() => setCartOpen(true)}>
                <Badge badgeContent={cartItems.length} color="primary">
                  <ShoppingCart />
                </Badge>
              </IconButton>

              {/* 通知图标 */}
              <IconButton color="inherit">
                <Badge badgeContent={4} color="error">
                  <Notifications />
                </Badge>
              </IconButton>

              {/* 用户头像菜单 */}
              <NavMenu />
            </Box>
          </Toolbar>
        </AppBar>

        {/* Toolbar spacing */}
        <Toolbar />

        {/* Main content area */}
        <Box 
          component="main" 
          sx={{ 
            flexGrow: 1,
            p: 3,
            overflow: 'auto',
            backgroundColor: (theme) => theme.palette.grey[100]
          }}
        >
          {children}
        </Box>
      </Box>

      <CartDrawer
        open={cartOpen}
        onClose={() => setCartOpen(false)}
        items={cartItems}
      />
    </Box>
  );
};

export default DashboardLayout; 